.main-24UiG {
  width: 100%;
  counter-reset: section 0;
  padding: 0px 150px;
  margin: 0px auto;
}
h1,
h2,
h3,
h4,
h5 {
  font-weight: 600;
  color: var(--c-white);
  margin: 0 0 10px 0;
}
img {
  width: 100%;
  max-width: 100%;
  vertical-align: middle;
}
svg {
  width: 100%;
  height: 100%;
  fill: currentColor;
  vertical-align: middle;
}
a {
  display: inline-block;
  text-decoration: none;
  text-decoration-skip-ink: auto;
  color: inherit;
  position: relative;
  transition: var(--transition);
  cursor: pointer;
}
a:hover,
a:focus {
  color: var(--c-green);
  outline: 0;
}
button {
  cursor: pointer;
  border: 0;
  border-radius: 0;
}
button:focus,
button:active {
  outline-color: var(--c-blue);
}
input,
textarea {
  border-radius: 0;
  outline: 0;
}
input:focus,
textarea:focus {
  outline: 0;
}
input:focus::placeholder,
input:active::placeholder,
textarea:focus::placeholder,
textarea:active::placeholder {
  opacity: 0.5;
}
p {
  margin: 0 0 15px 0;
}
ul,
ol {
  padding: 0;
  margin: 0;
  list-style: none;
}
@media (max-width: 1000px) {
  .main-24UiG {
    padding: 0px 100px;
  }
}
@media (max-width: 768px) {
  .main-24UiG {
    padding: 0px 50px;
  }
}
@media (max-width: 480px) {
  body {
    font-size: var(--fs-lg);
  }
  .main-24UiG {
    padding: 0px 25px;
  }
}
