html {
  scroll-behavior: smooth;
  box-sizing: border-box;
  width: 100%;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  width: 100%;
  min-height: 100%;
  overflow-x: hidden;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  background-color: var(--bg-c-navy);
  color: var(--c-slate);
  line-height: 1.3;
  font-family: var(--font-Calibre);
  font-size: var(--fs-xl);
}

.hidden {
  overflow: hidden;
}

/* App Scrollbar */
/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: var(--bg-c-navy);
}

/* Handle */
::-webkit-scrollbar-thumb,
::-webkit-scrollbar-thumb:hover {
  background: var(--bg-c-green);
  border-radius: 5px;
}

/* Selection color  */

::selection {
  background-color: var(--highlight);
}
